.container {
  display: flex; /* Flexbox   */
  flex-direction: row; /*    */
  width: 100%;
  min-height: 100vh;
}

.map-container {
  flex: 1;  /*     */
  width: 100%;
  height: 100vh; /*    */
  position: relative;
  margin-left: 250px; /*      */
}

.map-container #map {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.menu-container {
  z-index: 99999; 
  left: 0;
  top: 0; 
  position: fixed; 
  width: 250px; /*     */
  height: 100%; 
}

.menu-screen {
  display: flex; /* Flexbox   */
  flex-direction: column; /*    */
  justify-content: flex-start; /*    ()  */
  align-items: center; /*      */
  height: 100%;
  width: 100%;
  background-color: #20493C;
}

.menu-title {
  color: #ffffff;
  margin-top: 35px;
  margin-bottom: 15px;
  font-size: 40px;
}

.menu-id {
  color: #ffffff;
  align-self: flex-end;
  margin-bottom: 60px;
  margin-right: 10px;
  font-size: 20px;
}

.menu-button {
  background: #ffffff 50% no-repeat !important;
  color: rgb(0, 0, 0) !important;
  border: none;
  padding: 10px 20px;
  margin-top: 10px;
  font-size: 16px;
  cursor: pointer;
  border-radius: 5px;
  width: 75%;
  height: 47px;
  font-weight: bold;
}

.menu-button:hover {
  background-color: #45a049; /*      */
}

.menu .line_on {
  background: url(../../public/images/train/select_box.png) 50% no-repeat;
  width: 180px;
  height: 50px;
  cursor: pointer;
}

/*   */
@media (max-width: 600px) {
  .container {
    flex-direction: column;
    min-height: auto;
  }

  .map-container {
    margin-left: 0;
    height: 50vh; /*     */
  }

  .menu-container {
    width: 100%;
    height: auto;
    position: relative;
  }

  .menu-screen {
    padding: 10px;
  }

  .menu-title {
    font-size: 24px;
    margin-top: 20px;
    margin-bottom: 10px;
  }

  .menu-id {
    font-size: 14px;
    margin-bottom: 30px;
  }

  .menu-button {
    font-size: 14px;
    width: 90%;
    height: 40px;
  }

  .map-container #map {
    height: 100%;
  }
}
